<template>
    <div class="modal fade" id="exampleModal" tabindex="-1" aria-labelledby="exampleModalLabel" aria-hidden="true" ref="modal">
        <div class="modal-dialog modal-xl" role="document" >
            <div class="modal-content border-0">
                <div class="modal-header back_modal_header">
                    <h3 class="modal-title fw-bold" id="exampleModalLabel">
                        <slot name="modalCategory"></slot>
                    </h3>
                    <button type="button" class="btn-close"
                            data-bs-dismiss="modal" aria-label="Close">
                    </button>
                </div>
                <div class="modal-body">
                    <div class="row">
                        <div class="col-sm-4">
                            <div class="mb-3">
                                <label for="image" class="form-label">輸入封面圖片網址</label>
                                <input 
                                    type="text" 
                                    class="form-control" 
                                    id="image"
                                    placeholder="請輸入圖片連結"
                                    v-model="tempProduct.imageUrl">
                            </div>
                        <div class="mb-3">
                            <label for="customFile" class="form-label">或 上傳圖片
                            <i class="fas fa-spinner fa-spin"></i>
                            </label>
                            <input 
                                type="file" 
                                id="customFile" 
                                class="form-control"
                                ref="fileInput"
                                @change="uploadFile">
                        </div>
                        <img class="img-fluid" alt="產品圖片" v-if="tempProduct.imageUrl" :src="tempProduct.imageUrl">
                        
                        </div>
                        <div class="col-sm-8">
                            <!-- 標題 -->
                            <div class="mb-3">
                                <label for="title" class="form-label">標題</label>
                                <input 
                                    type="text" 
                                    class="form-control" 
                                    id="title"
                                    placeholder="請輸入標題" 
                                    v-model="tempProduct.title">
                            </div>

                            <div class="row gx-2">
                                <!-- 分類 -->
                                <div class="mb-3 col-md-6">
                                    <label for="category" class="form-label">分類</label>
                                    <input type="text" 
                                    class="form-control" 
                                    id="category"
                                    v-model="tempProduct.category"
                                    placeholder="請輸入分類"
                                    >
                                </div>
                                <!-- 單位 -->
                                <div class="mb-3 col-md-6">
                                    <label for="price" class="form-label">單位</label>
                                    <input type="text" 
                                    class="form-control" 
                                    id="unit"
                                    placeholder="請輸入單位"
                                    v-model="tempProduct.unit">
                                </div>
                            </div>

                            <div class="row gx-2">
                                <!-- 原價 -->
                                <div class="mb-3 col-md-6">
                                    <label for="origin_price" class="form-label">原價</label>
                                    <input 
                                        type="number" 
                                        class="form-control" 
                                        id="origin_price"
                                        placeholder="請輸入原價"
                                        v-model="tempProduct.origin_price">
                                </div>
                                <!-- 售價 -->
                                <div class="mb-3 col-md-6">
                                    <label for="price" class="form-label">售價</label>
                                    <input 
                                        type="number" 
                                        class="form-control" 
                                        id="price"
                                        placeholder="請輸入售價"
                                        v-model="tempProduct.price">
                                </div>
                            </div>
                            <hr>
                            <!-- 產品描述 -->
                            <div class="mb-3">
                                <label for="description" class="form-label">產品描述</label>
                                <textarea 
                                    type="text" 
                                    class="form-control" 
                                    id="description"
                                    placeholder="請輸入產品描述"
                                    v-model="tempProduct.description">
                                </textarea>
                            </div>
                            <!-- 產品內容 -->
                            <div class="mb-3">
                                <label for="content" class="form-label">說明內容</label>
                                <textarea 
                                    type="text" 
                                    class="form-control" 
                                    id="content"
                                    placeholder="請輸入產品說明內容"
                                    v-model="tempProduct.content">
                                </textarea>
                            </div>

                            <div class="mb-3">
                                <div class="form-check">
                                    <input 
                                    class="form-check-input" 
                                    type="checkbox"
                                    :true-value="1"
                                    :false-value="0"
                                    id="is_enabled"
                                    v-model="tempProduct.is_enabled">
                                    <label class="form-check-label" for="is_enabled">
                                    是否啟用
                                    </label>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
                <div class="modal-footer">
                    <button type="button" class="btn btn-outline-secondary"
                            data-bs-dismiss="modal">取消
                    </button>
                    <button type="button" class="btn main_btn" @click="$emit('update-product',tempProduct)">確認</button>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import modalMixin from '@/mixin/modalMixin';
export default {
props:{
    product:{
        type:Object,
        default(){ return {}; },
    },
},
data() {
    return {
        tempProduct:{},
        modal:{}
    };
},
watch:{
    product(){
        this.tempProduct = this.product;
    },
},
methods: {
    uploadFile(){
        const uploadedFile= this.$refs.fileInput.files[0];
        const formData=new FormData();
        formData.append('file-to-upload',uploadedFile);
        const api=`${process.env.VUE_APP_API}api/${process.env.VUE_APP_PATH}/admin/upload`;
        this.$http.post(api,formData)
        .then((res)=>{
            console.log(res.data);
            if(res.data.success){
                this.tempProduct.imageUrl = res.data.imageUrl;
                this.$refs.fileInput.value ='';
            }
        });
    },
},
mixins:[modalMixin],
}
</script>